// General styling
html {
    font-size: 14px;
}
body {
    font-size: 1rem;
    font-family: 'Open Sans', Arial ,sans-serif;
}
section {
    box-sizing: border-box;
    padding: 30px 10px;
    text-align: left;
}
h3 {
    font-family: 'Merriweather', Arial ,sans-serif;
    color: $secondary-color;
    font-weight: 300;
}
h2 {
    color: $main-color;
    text-transform: uppercase;
    font-weight: 400;
}
p {
    color: $paragraph-color;
}
.section-header {
    &:before {
        content: " ";
        border-right: 2px solid #da491c;
        margin-right: 10px;
    }
}
.bg-theme {
    background: $background-color-1;
}
.bg-theme-darker {
    background: $background-color-2;
}
.bg-theme-black {
    background: black;
}
.bg-theme-subtle-dark {
    background: #333;
}
.bg-theme-subtle-grey {
    background: #383838;
}
.btn-style {
    border-radius: 5px;
    color: $main-color;
    border: none;
    outline: none;
    text-transform: uppercase;
}
.second-btn-style {
    border-radius: 10px;
    color: $main-color;
    background: transparent;
    border: 1px solid $paragraph-color;
    outline: none;
}
.underline-decoration {
    display: block;
    height: 20px;
    width: 20%;
    border-bottom: solid 2px $distinct-color;
    margin: 0 auto;
}
// Main styling
.menu {
    li {
        font-size: 1.5rem;
        padding: 5px 0;
        @media screen and (orientation: landscape) {
            display: inline-block;
            width: 49%;
        }
        a {
            display: block;
            &:hover {
                color: $distinct-color;
            }
        }
    }
}
.main {
    padding: 5px 15px 40px;
    color: $main-color;
    text-align: center;
    h1 {
        text-transform: uppercase;
        font-weight: normal;
        span {
            font-weight: bold;
        }
    }
    p {
        color: $main-color;
    }
}
.text-cursor {
    border-right: solid 2px #9b9b9b;
    padding-right: 3px;
    animation: blink-animation 1s steps(5, start) infinite;
    -webkit-animation: blink-animation 1s steps(5, start) infinite;
    @keyframes blink-animation {
        to {
            visibility: hidden;
        }
    }
    @-webkit-keyframes blink-animation {
        to {
            visibility: hidden;
        }
    }
}
.menu-bar {
    div {
        display: flex;
        justify-content: space-between;
        span {
            font-weight: bold;
            font-size: 2rem;
        }
    }
    ul {
        padding: 0;
        text-transform: uppercase;
        list-style: none;
    }
}
.sticky {
    position: fixed;
    width: 100%;
    background: rgba(153, 153, 153, 0.9);
    z-index: 2;
    top: 0;
    left: 50%;
    padding: 0 15px;
    transform: translate(-50%,0);
}
.showcase-btn {
    margin: 10px auto;
    padding: 0 10px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 180px;
    background: #393939;
    height: 50px;
}
.showcase-section {
    padding-bottom: 0;
    .underline-decoration {
        margin: 0;
    }
}
.triangle {
    border-style: solid;
    border-width: 5px 0 5px 8.7px;
    border-color: transparent transparent transparent #fff;
    display: inline-block;
}
.aside-div {
    background: #666;
    height: 300px;
}
.speciality-section {
    .icon-div {
        display: flex;
        flex-direction: column;
        text-align: center;
        border: 1px solid $background-color-2;
        img {
            padding: 30px 0;
        }
        figure {
            margin: 1em 0;
            figcaption {
                text-transform: uppercase;
                color: $main-color;
                font-weight: 600;
            }
        }
    }
}
.hidden-content {
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.5s;
    span {
        background: $distinct-color;
        height: 55px;
        display: flex;
        align-items: center;
        justify-content: center;
        img {
            padding: 0 !important;
            max-width: 20px;
            max-height: 20px;
        }
    }
}
.content-shown {
    max-height: 300px;
    transition: max-height 1s;
}
.content-bg {
    background: $background-color-2;
    transition: background 0.2s;
}
.team-section {
    div {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        text-align: center;
        &:hover {
            background: $distinct-color !important;
        }
        p {
            color: $main-color;
            width: 100%;
            span {
                text-transform: uppercase;
                &:last-of-type {
                    font-size: 0.7rem;
                }
            }
        }
        background: $paragraph-color;
        height: 200px;
        &:first-of-type,
        &:last-of-type {
            background: #666;
        }
        figure {
            margin: 10px auto;
            img {
                padding: 0 5px;
            }
        }
    }
}
.how-we-do-section {
    table {
        margin-top: 50px;
        position: relative;
        text-align: center;
        td {
            padding: 5px;
            &:nth-child(odd) {
                border-right: 1px dashed #676766;
            }
            &:nth-child(even) {
                border-left: 1px dashed #676766;
            }
            p {
                &:first-of-type {
                    color: $main-color;
                    font-weight: bold;
                    text-transform: uppercase;
                }
            }
        }
    }
    .desktop-table {
      display: none;
    }
}
.discover-span {
    background: $distinct-color;
    border-radius: 50%;
    display: flex;
    width: 60px;
    height: 60px;
    position: absolute;
    top: -4%;
    right: 50%;
    margin-right: -30px;
    img {
        margin: auto;
    }
}
.design-span {
    @extend .discover-span;
    top: 23%;
}
.develop-span {
    @extend .discover-span;
    top: 48%;
    border: 1px solid $distinct-color;
    background: $background-color-1;
}
.deploy-span {
    @extend .discover-span;
    top: 73%;
}
.statistics-section {
    p {
        color: $main-color;
    }
}
.statistic-wrapper {
    text-align: center;
    display: flex;
    align-items: center;
    margin: 10px 0;
    p {
        margin: 10px 0;
        font-weight: bold;
        font-size: 2.5rem;
        line-height: 0.8;
        span {
            font-weight: normal;
            font-size: 1.2rem;
        }
    }
    &:not(last-of-type) {
        display: block;
    }
}
%styled-before {
    &:before {
        content: "Quick six blind smart out burst out";
        font-size: 0.7rem;
        color: #747474;
        display: block;
        text-align: left;
        width: 100%;
    }
}
%section-style-underline {
    border-bottom: 1px solid #747474;
    margin: 0 auto;
}
.video-section-header {
    @extend %section-style-underline;
    @extend %styled-before;
    padding: 5px 0;
}
.description-wrapper {
    p {
        &:first-child {
            font-weight: 600;
            font-size: 1.17rem;
            color: $main-color;
        }
        span {
            color: $distinct-color;
        }
    }
}
.iframe-maxWidth {
    max-width: 720px;
    margin: 0 auto;
}
.videoWrapper {
    position: relative;
    padding-bottom: 56.25%;
    padding-top: 25px;
    height: 0;
}
.video-placeholder {
    border: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.FAQ {
    h2 {
        text-transform: capitalize;
    }
}
.FAQ-desc {
    @extend %section-style-underline;
    color: $main-color;
    padding: 5px 0;
    img {
        padding-right: 5px;
    }
}
.FAQ-question {
    cursor: pointer;
    display: flex;
    flex-direction: column;
    @extend %section-style-underline;
    div {
        display: flex;
        align-items: center;
        img {
            padding-right: 5px;
        }
        p {
            color: $main-color;
        }
    }
}
.content-holder {
    margin: 10px 0;
}
.features-desc-div {
    text-align: left;
    min-width: 280px;
    width: 70%;
    margin: 0 auto;
    &:nth-child(even) {
        text-align: right;
    }
    p {
        &:first-of-type {
            color: $main-color;
            text-transform: uppercase;
        }
    }
}
.work-section {
    div {
        text-align: center;
    }
}
.featured-btns {
    border-radius: 25px;
    min-width: 60px;
    text-transform: uppercase;
    padding: 5px;
    margin: 15px 0;
    &:hover {
        background: $distinct-color;
        border: 1px solid $distinct-color;
    }
    &:last-of-type {
        margin: 0 0 15px;
    }
}
.works {
    height: 200px;
    background: $paragraph-color;
    display: flex;
    justify-content: center;
    align-items: center;
    &:hover {
        background: black !important;
    }
    &:nth-child(odd) {
        background: #737373;
    }
    figure {
        img {
            margin: 10px -4px;
        }
        figcaption {
            color: $main-color;
        }
    }
}
.works-spread {
    margin: 2px;
}
.brand-logo {
    background: black;
    height: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 2px 0;
    p {
        padding: 0 10px;
        color: $main-color;
        font-size: 0.8rem;
        text-align: center;
    }
    &:hover {
        background: $distinct-color;
        transition: all 0.5s;
    }
}
.vertical-slider {
    text-align: center;
    height: 250px;
    overflow: hidden;
    min-width: 280px;
    margin: 0 auto;
    ul {
        position: relative;
        padding: 0;
        margin: 0;
        li {
            display: flex;
            height: 250px;
            width: 100%;
            margin: 0 auto;
        }
    }
}
.slider-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    p {
        font-family: 'Merriweather', Arial ,sans-serif;
        color: $main-color;
        margin: 0 0 10px;
    }
    span {
        padding: 20px 0;
        color: #999;
    }
}
.slider-btns {
    display: flex;
    justify-content: center;
}
#nextQuote,
#prevQuote {
    margin: 10px 0;
    padding-top: 2px;
    display: flex;
    height: 30px;
    width: 30px;
    background: #ccc;
    color: #fff;
    cursor: pointer;
    justify-content: center;
    align-items: baseline;
    font-size: 1.5rem;
    &:active {
        background: $distinct-color;
    }
}
#prevQuote {
    transform: rotate(180deg);
}
.section-header-holder {
    display: flex;
    justify-content: flex-start;
    span {
        margin: 0;
    }
}
.mobile-design-section {
    h1 {
        color: $main-color;
        margin: 0 0 15px;
        text-transform: uppercase;
    }
}
.img-wrapper {
    width: 40%;
    margin: 0 auto;
    max-width: 260px;
    img,
    picture,
    source {
        width: 100%;
    }
}
.btn-holder {
    text-align: center;
    a {
        display: inline-block;
    }
    p {
        text-align: left;
    }
}
.m-d-btn {
    width: 140px;
    margin: 10px 5px;
    background: #393939;
    padding: 10px;
    &:hover {
        background: $distinct-color;
    }
}
.m-d-hidden {
    display: none;
}
%paragraph-style {
    border-radius: 25px;
    margin: 10px auto;
    padding: 10px 0;
}
.plan-select {
    @extend %paragraph-style;
    text-align: center;
    font-size: 1.6rem;
    width: 240px;
    color: $main-color;
}
.plan-options {
    text-align: center;
    text-transform: uppercase;
    cursor: pointer;
    span {
        display: inline-block;
        width: 30%;
        padding: 15px;
        color: $paragraph-color;
        border-radius: 25px;
    }
}
.plan-length {
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    @extend %paragraph-style;
    padding: 0;
    border: 2px solid #515151;
    color: $main-color;
    width: 220px;
    span {
        display: flex;
        border-radius: 25px;
        width: 100px;
        height: 40px;
        justify-content: center;
        align-items: center;
    }
}
.selected {
    background: $distinct-color !important;
    color: $main-color !important;
}
.table-wrapper {
    border-radius: 5px;
    overflow: hidden;
    margin: 10px auto;
    min-width: 280px;
    width: 60%;
    height: 433px;
}
.movable {
    position: relative;
    width: 100%;
    height: 433px;
}
.price-wrapper {
    display: flex;
    height: 35px;
    justify-content: center;
    span {
        display: flex;
        font-size: 1.2rem;
        color: #999;
        &:nth-child(even) {
            align-items: center;
            font-size: 2.5rem;
            color: black;
        }
        &:first-child {
            align-items: flex-start;
        }
        &:last-child {
            align-items: flex-end;
        }
    }
}
.colored {
    color: $distinct-color !important;
}
.bg-colored {
    background: $distinct-color !important;
}
.plan-desc {
    width: 100%;
    text-align: center;
    tr {
        &:nth-child(odd) {
            background: $main-color;
        }
        &:nth-child(even) {
            background: #f1f4f5;
        }
        &:first-child {
            text-transform: uppercase;
            p {
                color: black;
                font-size: 1.5rem;
                margin: 10px 0;
                sub,
                sup {
                    font-size: 1.5rem;
                    color: $paragraph-color;
                }
                span {
                    font-size: 1.5rem;
                }
            }
        }
        &:last-child {
            text-transform: uppercase;
            background: black;
            color: $main-color;
            line-height: 2;
            cursor: pointer;
        }
        &:nth-last-child(2) {
            height: 80px;
            margin: 0 auto;
            vertical-align: top;
        }
    }
    td {
        padding: 10px;
    }
}
.article-holder {
    background: #6b6b6b;
    display: flex;
    align-items: flex-end;
    flex-wrap: wrap;
    padding: 15px 10px;
    border: 1px solid #333;
    &:hover {
        background: #222;
    }
}
.article-info {
    color: $main-color;
    span {
        color: $distinct-color;
    }
}
.article-title {
    color: $main-color;
    font-size: 1.5rem;
}
.article-fullContent {
    cursor: pointer;
    p {
        text-align: center;
        text-transform: uppercase;
        color: $main-color;
    }
}
.contact-section {
    h3 {
        color: $main-color;
    }
}
.contact-info {
    display: flex;
    text-align: center;
    div {
        width: 54%;
        &:first-child {
            width: 46%;
        }
    }
    span {
        color: $main-color;
    }
}
.message-content {
    label {
        color: #999;
        display: flex;
        flex-direction: column;
        padding: 5px 0;
        text-align: left;
        min-width: 280px;
        max-width: 70%;
        margin: 0 auto;
        input,
        select {
            height: 40px;
        }
        textarea {
            resize: none;
        }
    }
}
.message-btn {
    @extend .showcase-btn;
    background: #292929;
}
.footer-nav {
    color: $main-color;
    padding: 5px 0;
    ul {
        max-width: 280px;
        list-style: none;
        text-transform: uppercase;
        display: flex;
        flex-flow: row wrap;
        justify-content: space-between;
        padding: 0;
        margin: 0 auto;
        li {
            padding: 5px;
            margin: 0 5px;
            a {
                &:hover {
                    color: $distinct-color;
                }
            }
        }
    }
}
.footer-mail-content {
    padding: 15px 0 15px 10px;
    margin: 0 auto;
    max-width: 280px;
    input {
        font-size: 1.6rem;
        font-weight: 300;
        display: flex;
        justify-content: flex-start;
        border: none;
        background: $background-color-2;
        color: $secondary-color;
        outline: 0;
    }
    span {
        border-top: 1px solid $paragraph-color;
        color: $paragraph-color;
        display: block;
        min-width: 260px;
        width: 80%;
        text-align: left;
    }
}
.footer-desc {
    text-align: center;
    padding: 5px 10px;
    span {
        color: $main-color;
        font-size: 1.5rem;
    }
    p {
        min-width: 260px;
        width: 40%;
        margin: 0 auto;
    }
}
.social-media {
    margin: 10px;
    img {
        margin: 4px;
        height: 24px;
        width: 24px;
    }
}
.footer-copyright {
    display: block;
    text-align: center;
    padding-top: 25px;
    color: $paragraph-color;
}
