@include breakpoint(large-desktop) {
    .aside-div {
        height: 480px;
    }
    .speciality-section {
        padding: 0 15px;
    }
    .header-content {
        padding-top: 30px;
    }
    .icon-div {
        height: 300px;
    }
    .statistics-section {
        position: relative;
        height: 400px;
        div {
            text-align: left;
        }
        .aside-div {
            height: 450px;
            clear: none;
            position: absolute;
            right: 15px;
            bottom: 0;
        }
    }
    .statistic-wrapper {
        display: flex !important;
        justify-content: flex-start;
        img {
            padding-right: 15px;
        }
    }
    .team-section {
        div {
            height: 400px;
        }
    }
    .how-we-do-section {
        table {
            display: none;
        }
        height: 600px;
        .desktop-table {
            display: table;
            tr {
                &:first-of-type {
                    td {
                        border-bottom: 1px dashed #676766 !important;
                        position: relative;
                        &:after {
                            position: absolute;
                            bottom: -8px;
                            left: -8px;
                            content: " ";
                            width: 16px;
                            height: 16px;
                            border-radius: 50%;
                            background: $main-color;
                            display: block;
                        }
                    }
                }
                td {
                    text-align: left;
                    border-right: 1px solid #676766;
                    border-left: 1px solid #676766;
                    &:last-of-type {
                        border-right: none !important;
                    }
                }
            }
        }
    }
    .discover-span {
        top: -30px;
        left: 0;
        margin-left: -30px;
    }
    .design-span {
        top: auto;
        bottom: -170px;
        left: 284px;
    }
    .develop-span {
        left: 570px;
    }
    .deploy-span {
        top: auto;
        right: 0;
        bottom: -170px;
        margin-left: auto;
    }
    .mid-section-bg {
        background: #333;
        min-height: 400px;
        max-height: 1000px;
        clear: both;
        overflow: hidden;
    }
    .description-wrapper,
    .header-wrapper {
        padding: 0 15px;
    }
    .iframe-maxWidth {
        max-width: 100%;
    }
    .videoWrapper {
        padding: 0;
        height: 400px;
    }
    .FAQ {
        padding: 0 15px;
    }
    .works {
        &:nth-child(odd) {
            background: $paragraph-color;
        }
        &:nth-child(even) {
            background: $paragraph-color;
        }
        &:nth-child(-2n+4) {
            background: #737373;
        }
        &:nth-child(2n+5) {
            background: #737373;
        }
    }
    .brand-logo {
        height: 300px;
    }
    .vertical-slider {
        ul {
            li {
                width: 60%;
            }
        }
    }
    .mobile-design-section {
        position: relative;
        overflow: hidden;
        height: 550px;
        transition: height 1s ease-out;
    }
    .btn-holder {
        position: absolute;
        top: 250px;
        width: 420px;
    }
    .m-d-btn {
        &:first-child {
            margin-right: 15px;
        }
        margin: 0;
        padding: 25px;
        width: 200px;
    }
    .m-d-hidden {
        display: block;
        height: 0;
        overflow: hidden;
        transition: height 1s ease-out;
    }
    .m-d-section-animate {
        height: 920px;
        transition: height 1s ease-in;
    }
    .m-d-content-animate {
        height: 580px;
        transition: height 1s ease-in;
    }
    .article-holder {
        padding: 160px 20px 80px;
        height: 350px;
        align-items: flex-end;
    }
    .footer-nav {
        ul {
            max-width: 1170px;
            li {
                width: auto;
            }
        }
    }
}
